.info-main {
  font-family: Barlow;
  font-style: normal;
  font-weight: normal;
  color: #FFFFFF;
  font-size: 16px;
  padding: 0 32px;
  margin-top: 50px;
  .title {
    font-size: 32px;
    a:hover {
      text-decoration: underline;
    }
  }
  .recharts-tooltip-wrapper {
    visibility: visible!important;
  }
  .charts {
    display: flex;
    margin-top: 40px;
    justify-content: space-between;
    display: flex;
    .graph {
      width: 49%;
      background: rgba(85, 119, 253, 0.1);
      border: 1px solid #203B60;
      height: 440px;
      display: flex;
      align-items: flex-end;
      .cycle-c {
        display: flex;
        justify-content: flex-end;
        margin-bottom: 70px;
        margin-right: 32px;
        .filter{
          justify-content: flex-end;
          .g-button {
            margin-left: 8px ;
          }
        }
      }
    }

  }
  .list-by-chain {
    margin-bottom: 112px;
    .table {
      .rc-table-cell {
        min-width: 100px;
        text-align: center;
      }
    }
  }
}
@media only screen and (max-width: 760px) {
  .info-main {
    .charts {
      display: flex;
      margin-top: 20px;
      justify-content: space-between;
      display: flex;
      flex-direction: column;
      .graph {
        width: 100%;
        margin-top: 24px;
      }
    }
    .list-by-chain {
      margin-bottom: 112px;
      .table {
        .rc-table-cell:first-child{
          padding-left: 24px;
        }
        .rc-table-cell {
          min-width: 100px;
          text-align: left;
        }
      }
    }
  }
}